import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    items: [],
    baseURL: 'https://photoshut-v1.herokuapp.com/api/v1',
    // baseURL: 'http://localhost:3001/api/v1',
    limit: 10,
    page: 1,
    loading: {
      titulo: '',
      estado: false,
      color: 'primary'
    }
  },
  mutations: {
    addComment (id) {
      console.log(`Le dí a comment ${id}`)
    },
    addLike (id) {
      console.log(`Le dí like a ${id}`)
    },
    mostrarLoading (state, payload) {
      state.loading.titulo = payload.titulo
      state.loading.color = payload.color
      state.loading.estado = true
    },
    ocultarLoading (state) {
      state.loading.estado = false
    },
    getLimit (id) {
      if (id) {
        return id
      } else {
        return 10
      }
    },
    getPage (id) {
      if (id) {
        return id
      } else {
        return 1
      }
    }
  },
  actions: {
    async getImages () {
      const res = await this.axios.get(`${this.baseURL}/images/limit/${this.limit}/page/${this.page}`)
      this.items = res.data.images
    },
    async getImagesUsers () {
      this.mostrarLoading({ titulo: 'Accediendo a información', color: 'secondary' })

      const res = await this.axios.get(`${this.baseURL}/users/${this.id}/images`)
      
      if (res.data.images.length != 0) {
        this.items = await res.data.images
      } else {
        this.items = 'No hay imágenes'
        console.log(this.items)
      }
    }
  }
})
