<template>
  <div id="app">

    <navbar></navbar>

    <div class="container mt-5">
      <adds></adds>
    </div>

    <div class="container mt-4 mb-0 pb-0">
      <div class="row">

        <router-view></router-view>

        <div class="col-md-6 col-lg-4 col-xl-3 d-flex justify-content-center py-5" v-if="loading.estado === false">
          <div class="spinner-grow text-info" role="status" color="primary" style="width: 4rem; height: 4rem;">
            <span class="sr-only">{{ loading.titulo }}</span>
          </div>
        </div>
        
        <section class="col-md-12">
          <adds></adds>
        </section>
      </div>

    </div>

    <footer></footer>

  </div>
</template>

<script>
import { mapState } from 'vuex'

import Adds from '@/components/Adds.vue'
import Footer from '@/components/Footer.vue'
import Navbar from '@/components/Navbar.vue'

export default {
  components: {
    Adds,
    Footer,
    Navbar
  },
  data () {
    return {
      items: [],
      baseURL: 'https://photoshut-v1.herokuapp.com/api/v1',
      // baseURL: 'http://localhost:3001/api/v1',
      limit: 10,
      page: 1
    }
  },
  created () {
    this.getImages()
  },
  methods: {
    async getImages () {
      const res = await this.axios.get(`${this.baseURL}/images/limit/${this.limit}/page/${this.page}`)
      this.items = res.data.images
    }
  },
  computed: {
    ...mapState(['loading'])
  }
}
</script>

